* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family:'Times New Roman', Times, serif;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.header {
    text-align: center;
    border-bottom: 2px solid #82001a;
    padding: 10px 0;
    background-color: #f8f8f8;
    flex: 0 0 15vh;                                         
}

.header img {
    vertical-align: middle;
    width: 80px;
}

.header h2 {
    display: inline;
    color: #82001a;
    margin-left: 10px;
    font-size: 1.5em;
}

.main {
    flex: 1; 
    display: flex;
    justify-content:right;
    background-image:url(https://www.igauge.in/admin/uploaded/rating/VallurupalliNageswaraRaoCampusFacebook_1610429815.png);
}

.main img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain; 
}

.footer {
    border-top: 2px solid #82001a ;
    color: white;
    text-align: center;
    padding: 10px 0;
    flex: 0 0 15vh; 
}

.footer button {
    width: 20%;
    height:60%;
    background-color:#c2969f;
    color:blue;
    border:none;
    border-radius: 5px;
    padding: 10px 20px;
    margin: 10px;
    cursor: pointer;
    font-size: 1.1rem;
   }
  .main h3{
    font-size: 1.5rem;
    color:#82001a;
    text-align: right;
    padding-top: 10px;
    padding-right: 15px;
    text-decoration: underline;
   } 
   
.main h3:hover{
    
    cursor: pointer;
    color:blue;
    
}
.footer button:hover {
    
    background-color: #c47e8c;
}
